
//react 基础组件加载
import * as React from "react";
import { Button } from 'antd-mobile';
import qs from 'qs'

//页面样式加载
import DetailTitle from '../components/detailTitle';
import CardPanl from '../components/CardPanl'
import FootAdvertising from '../components/advertising/footAdvertising'
import DownLoad from '../components/down/downLoad'
import '../assets/less/coursedetail.less';

import { getVipDetail } from '../api/vip'
import { getCourseActiveTime } from '../util'

interface ISprops {
    [propName: string]: any;
}
interface IState {
    courseDetail: any,
    source: boolean,
    type: boolean,
    downShow: boolean
}
class ConsumerCourse extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            courseDetail: {},
            source: true,//设置来源 组建的个性化配置
            type: false,//消费详情进入为true
            downShow: false
        }
    }
    getCourseInfo = () => {//页面数据
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));

        let { id } = this.props.match.params;
        console.log(this.props)
        this.setState({
            type: data.type || false
        })
        getVipDetail({
            id
        }).then(res => {
            this.setState({
                courseDetail: res.data || []
            })
        })
        
    }
    componentDidMount() {
        document.title = '课程信息';
        this.getCourseInfo()
    }
    downLoad = () => {
        //下载大纲
        this.setState({
            downShow: true
        })
    }
    closeDownLoad = () => {
        this.setState({
            downShow: false
        })
    }
    render() {
        const { courseDetail, source, type, downShow } = this.state;
        console.log(courseDetail)
        return (
            <div className="course-detail" style={{ position: 'relative' }}>
                <div className="detail-banner">
                    <img src={courseDetail.img_url} alt="" />
                </div>
                
                <div className="tabs-detail">
                    <DetailTitle course={courseDetail} source={source} />
                    <CardPanl title="课程介绍">
                        <p>{courseDetail.course_content}</p>
                    </CardPanl>
                    <CardPanl title="讲师介绍">
                        <p>{courseDetail.teacher}</p>
                        <p>{courseDetail.teacher_content}</p>
                    </CardPanl>
                    <CardPanl title="课程信息">
                        <p style={{marginBottom:"0"}}>天数：{courseDetail.days}</p>
                        <p style={{marginBottom:"0"}}>时间：{getCourseActiveTime(courseDetail.start_time,courseDetail.end_time)}</p>
                        <p style={{marginBottom:"0"}}>省市：{courseDetail.province ? `${courseDetail.province}-${courseDetail.city}` : courseDetail.city}</p>
                        <p style={{marginBottom:"0"}}>{`计费：${courseDetail.amount}`} {courseDetail.course_count === 0 ? `` : `计次：${courseDetail.course_count}` }</p>
                    </CardPanl>
                    {
                        (courseDetail.course_file_name && courseDetail.course_file_url) ? 
                            <CardPanl title="课程大纲">
                                <p>{courseDetail.course_file_name}</p>
                                <div className="detail-c-b clearfix">
                                    <a className="left"  target="_blank" type="primary"  rel="noopener noreferrer" href={`${courseDetail.course_file_url}`}>在线查看</a>
                                    <Button className="left" type="primary" size="small" onClick={this.downLoad}>下载大纲</Button>
                                </div>
                            </CardPanl>
                            :
                            <div></div>
                    }
                    <FootAdvertising  title="course-detail"/>

                </div>

                <DownLoad title="大纲" downShow={downShow} detail={courseDetail} closeDownLoad={this.closeDownLoad} downLoad={this.downLoad}/>
            </div>
        );
    }

}

export default ConsumerCourse