import * as React from "react";
import { Modal, Toast } from 'antd-mobile'
import { sendSimpleMail, sendMainMail } from '../../api/course'

import DownItem from './downItem'
import './down.less'
interface ISprop {
    [propsName: string]: any
}
interface IState {
    [statesName: string]: any
}


export default class DownLoad extends React.Component<ISprop, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            modal1: false,
            emailArr: []
        }
    }
    componentDidMount() {
    }
    onClose(modal1) {
        this.props.closeDownLoad()
        this.setState({
            emailArr: []
        })
    }
    changeEmail = (item) => {
        let arr:any = [];
        (item || []).map( (el,index) => {
            arr.push(el.value)
        })
        this.setState({
            emailArr: arr
        })
    }
    submitMail = () => {
        this.onClose('modal1')
        let { emailArr } = this.state;
        let emailCheck = true;
        var reg = /^([a-zA-Z]|[0-9]|\.)(\w|\-|\.)+@[a-zA-Z0-9]/;

        if (!emailArr.length) {
            Toast.fail("邮箱不能为空",2);
            return false;
        }
        emailArr.map((el,index) => {
            if(el.replace(/\s/g, '').length !== 0) {
                if(!reg.test(el)){
                    Toast.fail("邮箱格式不正确",2);
                    emailCheck = false;
                    return false;
                }
            }  
        })
        if (!emailCheck) return false;
        if (this.props.source) {//课程列表进入
            const { filter, query } = this.props;
            let params = {
                citys: filter.citys,
                emails: emailArr.join(','),
                course_name: query,
                end_time: filter.end_time,
                start_time: filter.start_time,
                series_id: filter.series_id
            }
            sendMainMail(params).then(res => {
                this.props.closeDownLoad()
                if(!res.status) {
                    Toast.info('发送成功')
                } else {
                    Toast.fail(res["message"],2)
                }
            })
        } else {//课程详情进入
            const { detail } = this.props;
            let params = {
                course_class_id: detail.id,
                course_file_name: detail.course_file_name,
                emails: emailArr.join(','),
                file_url: detail.course_file_url
            }
            sendSimpleMail(params).then( res => {
                this.props.closeDownLoad()
                if(!res.status) {
                    Toast.info('发送成功')
                } else {
                    Toast.fail(res["message"],2)
                }
            })
        }
    }
    render() {
        return (
            <div className="down-load">
                <Modal
                    visible={this.props.downShow}
                    transparent
                    maskClosable={false}
                    onClose={() => this.onClose('modal1')}
                    footer={[{ text: '取消', onPress: () => { this.onClose('modal1') } },
                            { text: '确定', onPress: () => this.submitMail() }
                            ]

                            }
                >
                <DownItem title={this.props.title || ''} change={(emails) => this.changeEmail(emails)}/>
                </Modal>
                
            </div>

        )
    }

}
