import * as React from "react";

import send from '../../assets/img/index/send.png'
import guanbi from '../../assets/img/index/guanbi.png'
import './down.less'
interface ISprop {
    [propsName: string]: any
}
interface IState {
    [statesName: string]: any
}


export default class DownItem extends React.Component<ISprop, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            item:[
                {
                    label: '收件邮箱',
                    value: ''
                },
                {
                    label: '收件邮箱',
                    value: ''
                }
            ]
        }
    }
    componentDidMount() {
    }
    addMail = () => {
        let {item} = this.state;
        item.push({
            label: '收件邮箱',
            value: ''
        })
        this.setState({
            item
        })
    }
    changeValue = (e,index) => {
        let { item } = this.state;
        item.map( (el,indexs) => {
            if (index === indexs) {
                el.value = e.target.value;
            }
        })
        this.setState({
            item
        },() => {
            this.props.change(this.state.item)
        })

    }
    delMail = (index) => {
        let { item } = this.state;
        item.splice(index,1)
        this.setState({
            item
        })
    }
    render() {
        const { item } = this.state;
        return (
            <div className="down-item">
                <img src={send} alt=""/>
                <p>发送{this.props.title}至指定邮箱</p>
                <div className="down-item-i">
                    {
                        item.map( (el,index) => {
                            return (
                                <div className="item-i" key={index}>
                                    <input type="text" onChange={ (e) => this.changeValue(e,index)} value={el.value} placeholder={`${el.label}${index+1}`}/>
                                    <img src={guanbi} onClick={() => this.delMail(index)} className="close" alt=""/>
                                </div>
                            )
                        })
                    }

                </div>
                <div onClick={this.addMail} className="add">
                    +新增收件箱
                </div>
            </div>

        )
    }

}
